import React, { useState, useEffect, useRef } from 'react'
import { Formik, useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import CitizenApplyApiList from '../../Components/CitizenApplyApiList'
import { json, Link, useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import TradeNonBlockingLoader from '../Contents/CitizenSafForm/NonBlockingLoader.js';
import NotificationComponent from '../CitizenDashboardPage/NotificationComponent'
import NonBlockingLoader from '../Contents/CitizenSafForm/NonBlockingLoader.js';
import ApiHeader from '../../Components/ApiList/ApiHeader';
import { MdAddAPhoto } from 'react-icons/md'
import BackendUrl from '../../Components/ApiList/BackendUrl';
import { GrDocumentPdf } from 'react-icons/gr'


function EditProfile() {

    const { api_getProfileDetails, api_editCitizenProfile } = CitizenApplyApiList()
    const navigate = useNavigate()

    const [openEditScreen, setopenEditScreen] = useState('hidden')
    const [fetchData, setfetchData] = useState()
    const [updateLbl, setupdateLbl] = useState()
    const [CloseForm, setCloseForm] = useState(false)
    const [show, setshow] = useState(false);
    const [profileImage, setprofileImage] = useState()
    const [profileImagePreview, setprofileImagePreview] = useState()

    const [photoViewUrl, setphotoViewUrl] = useState(null)
    const [aadharViewUrl, setaadharViewUrl] = useState(null)
    const [armedViewUrl, setarmedViewUrl] = useState(null)
    const [specialViewUrl, setspecialViewUrl] = useState(null)

    const [photoDoc, setphotoDoc] = useState(null)
    const [aadharDoc, setaadharDoc] = useState(null)
    const [armedDoc, setarmedDoc] = useState(null)
    const [specialDoc, setspecialDoc] = useState(null)

    const [specialStatus, setspecialStatus] = useState(false);
    const [armedStatus, setarmedStatus] = useState(false);

    const photoRef = useRef()
    const aadharRef = useRef()
    const armedRef = useRef()
    const specialRef = useRef()

    let baseUrl = BackendUrl




    const notify = (toastData, actionFlag) => {
        toast.dismiss();
        { actionFlag == 'success' && toast.success(toastData) }
        { actionFlag == 'notice' && toast.warn(toastData) }
        { actionFlag == 'error' && toast.error(toastData) }

    };

    const HEADER = () => {
        let token = window.localStorage.getItem("token");
        const header = {
            headers:
            {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
            }
        }
        return header;
    }

    const showLoader = (status) => {
        setshow(status);
    }


    const openEditProfile = () => {
        openEditScreen == 'hidden' ? setopenEditScreen('') : setopenEditScreen('hidden')

    }

    const handleCloseForm = () => {
        openEditProfile()
        // CloseForm == 'hidden' ? setCloseForm('') : setCloseForm('hidden')

    }


    //fetch loged citizen data
    useEffect(() => {
        showLoader(true);
        axios.post(`${api_getProfileDetails}`, {}, ApiHeader())
            .then(function (response) {
                console.log('getdata for my profile ....', response.data)
                if (response.data.status) {
                    setfetchData(response?.data?.data)
                    feedProfileData(response?.data?.data)

                    showLoader(false)
                } else {
                    showLoader(false);

                }
            })
            .catch(function (error) {
                showLoader(false);

                console.log('getdata for my profile errorrr.... ', error);
            })
    }, [])



    let fd = new FormData()

    fd.append("profileImage", profileImage)
    // fd.append("coverImage", coverImage)


    //method  to update  citizen data
    const modifyData = (values) => {
        console.log("values", values)
        let requestBody = {
            id: '',
            name: values?.name,
            mobile: values?.mobile,
            email: values?.email,
            aadhar: values?.aadhar,
            dob: values?.dob,
            gender: values?.gender,
            isSpeciallyAbled: '0',
            isArmedForce: '0',
            aadharDoc: values?.aadharDoc,
            armedForceDoc: values?.armedForceDoc,
            speciallyAbledDoc: values?.speciallyAbledDoc,
            photo: values?.photo
        }
        console.log('before submit edit profile...', requestBody)

        return
        axios.put(api_editCitizenProfile, requestBody, ApiHeader())
            .then(function (response) {
                console.log(" Submitted......", response);
                if (response.data.status == true) {
                    notify("Your profile updated Successfully", "success");
                    // setopenEditScreen('hidden')
                    navigate('/landingDashboard')


                }
                else {
                    notify("Something went wrong", "notice");
                    navigate('/landingDashboard')


                }
            })
            .catch(function (response) {
                console.log("Failed", response);
                notify("Failed to update", "error");
                setopenEditScreen('hidden')

            })

    }

    // FUNCTION TO FEED PROFILE DATA IN INPUT FIELDS FOR EDIT PURPOSE
    const feedProfileData = (data) => {
        formik.setFieldValue('name', data?.name)
        formik.setFieldValue('mobile', data?.mobile)
        formik.setFieldValue('email', data?.email)
        formik.setFieldValue('aadhar', data?.aadhar)
        formik.setFieldValue('dob', data?.dob)
        formik.setFieldValue('gender', data?.gender)
        formik.setFieldValue('isSpeciallyAbled', data?.isSpeciallyAbled == true ? '1' : '0')
        formik.setFieldValue('isArmedForce', data?.isArmedForce == true ? '1' : '0')
        formik.setFieldValue('aadharDoc', data?.aadharDoc)
        formik.setFieldValue('armedForceDoc', data?.armedForceDoc)
        formik.setFieldValue('speciallyAbledDoc', data?.speciallyAbledDoc)
        formik.setFieldValue('photo', data?.photo)

        setphotoViewUrl(`${baseUrl}/${data?.profile_photo}`)
    }

    const validationSchema = Yup.object().shape({
        name: Yup.string().required('Enter Your Name'),
        mobile: Yup.string().required('Enter Your Name'),
        email: Yup.string().required('Enter Your Name'),
        aadhar: Yup.string().required('Enter Your Name'),
        dob: Yup.string().required('Enter Your Name'),
        gender: Yup.string().required('Enter Your Name'),
        isSpeciallyAbled: Yup.string().required('Select specially abled or not'),
        isArmedForce: Yup.string().required('Select armed force or not'),
        aadharDoc: Yup.string().required('Enter Your Name'),
        armedForceDoc: Yup.string(),
        speciallyAbledDoc: Yup.string(),
        photo: Yup.string().required('Enter Your Name'),

    });

    const formik = useFormik({
        initialValues: {
            name: '',
            mobile: '',
            email: '',
            aadhar: '',
            dob: '',
            gender: '',
            isSpeciallyAbled: '',
            isArmedForce: '',
            aadharDoc: '',
            armedForceDoc: '',
            speciallyAbledDoc: '',
            photo: ''

        },
        enableReinitialize: true,
        onSubmit: (values) => {
            console.log('data at submit edit profile...', values)
            modifyData(values)


        },
        validationSchema

    })


    const setImage = (e) => {
        let name = e.target.name

        if (name == 'aadharDoc') {
            setaadharViewUrl(URL.createObjectURL(e.target.files[0]))
            setaadharDoc(e.target.files[0])
            return
        }
        if (name == 'armedForceDoc') {
            setarmedViewUrl(URL.createObjectURL(e.target.files[0]))
            setarmedDoc(e.target.files[0])
            return
        }
        if (name == 'speciallyAbledDoc') {
            setspecialViewUrl(URL.createObjectURL(e.target.files[0]))
            setspecialDoc(e.target.files[0])
            return
        }
        if (name == 'photo') {
            setphotoViewUrl(URL.createObjectURL(e.target.files[0]))
            setphotoDoc(e.target.files[0])
            return
        }
    };

    const handleChange = (e) => {
        let name = e.target.name
        let value = e.target.value
        // if (name == 'profileImage') {
        //     let file = e.target.files[0]
        //     setprofileImage(e.target.files[0])
        //     const reader = new FileReader()
        //     reader.onloadend = () => {
        //         setprofileImagePreview(reader.result)
        //     }
        //     reader.readAsDataURL(file)
        // }
        if (name == 'isSpeciallyAbled') {
            { value == '1' ? setspecialStatus(true) : setspecialStatus(false) }
        }
        if (name == 'isArmedForce') {
            { value == '1' ? setarmedStatus(true) : setarmedStatus(false) }
        }

        if (name == 'aadharDoc' || name == 'armedForceDoc' || name == 'speciallyAbledDoc' || name == 'photo') {
            setImage(e)
        }

    }


    return (
        <>
            {/************VIEW AND UPDATE ACCOUNT DETAIL****************/}
            <div className=''>
                <div>
                    <main className=" mt-9 w-full md:w-4/5 mx-auto">
                        <div className="mx-auto ">
                            {/* row */}
                            <div className="flex flex-wrap flex-row">
                                {/* <div className="flex-shrink max-w-full px-4 w-full">
                                    <p className="text-xl font-bold mt-3 mb-5">Profile</p>
                                </div> */}
                                <NonBlockingLoader show={show} />

                                <div
                                    x-data="{ open: false }"
                                    className="flex-shrink max-w-full px-4 w-full mb-6"
                                >

                                    {/* profile with background image */}
                                    <div className="bg-white darks:bg-gray-800 rounded-lg overflow-hidden shadow-lg mb-8">

                                        <div className="flex justify-center mt-10 relative">
                                            <a className=" group" href="javascript:;">
                                                <img
                                                    src={`${baseUrl}/${fetchData?.profile_photo}`}
                                                    className="rounded-full w-24 h-24 bg-gray-200 border-solid border-white border-2 -mt-3"
                                                />
                                                <button type='button'
                                                    title="Change avatar"
                                                    className="absolute left-1/2 top-1/2 transform -translate-x-1/2 -translate-y-1/2 opacity-0 group-hover:opacity-100 text-white darks:text-gray-900" onClick={openEditProfile}
                                                >
                                                    <svg
                                                        xmlns="http://www.w3.org/2000/svg"
                                                        width={16}
                                                        height={16}
                                                        fill="currentColor"
                                                        className="w-6 h-6 bi bi-camera"
                                                        viewBox="0 0 16 16"
                                                    >
                                                        <path d="M15 12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1h1.172a3 3 0 0 0 2.12-.879l.83-.828A1 1 0 0 1 6.827 3h2.344a1 1 0 0 1 .707.293l.828.828A3 3 0 0 0 12.828 5H14a1 1 0 0 1 1 1v6zM2 4a2 2 0 0 0-2 2v6a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2h-1.172a2 2 0 0 1-1.414-.586l-.828-.828A2 2 0 0 0 9.172 2H6.828a2 2 0 0 0-1.414.586l-.828.828A2 2 0 0 1 3.172 4H2z" />
                                                        <path d="M8 11a2.5 2.5 0 1 1 0-5 2.5 2.5 0 0 1 0 5zm0 1a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7zM3 6.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0z" />
                                                    </svg>
                                                </button>
                                            </a>
                                        </div>
                                        <div className="text-center px-3 pb-6 pt-2">
                                            <h3 className="text-gray-800 darks:text-gray-100 font-bold text-lg">
                                                {fetchData?.name}
                                            </h3>
                                            <p className="mt-2 font-light text-center">
                                                Welcome  to your profile
                                            </p>
                                        </div>

                                    </div>
                                    <div className="flex flex-wrap flex-row -mx-4">

                                        {/* information section */}
                                        <div className="flex-shrink max-w-full px-4 w-full  mb-8">
                                            <div className="bg-white darks:bg-gray-800 rounded-lg shadow-lg h-full p-6">
                                                <div className="flex flex-row justify-between 2xl:pb-3">
                                                    {/* <div className="flex flex-row w-full">
                                                        <div className='flex-1'>
                                                            <button className='shadow-xl cursor-pointer 2xl:text-base text-sm bg-indigo-500 text-white px-2 py-1 rounded-sm hover:bg-indigo-600' onClick={() => navigate('/change-password')}>Change Password</button>
                                                        </div>
                                                        <div className='flex-1 text-right'>
                                                            <button className='shadow-xl cursor-pointer 2xl:text-base text-sm bg-indigo-500 text-white px-2 py-1 rounded-sm hover:bg-indigo-600' onClick={() => navigate('/change-password')}>View Dashboard</button>
                                                        </div>
                                                    </div> */}
                                                    <div className="relative">
                                                        {/* <button type='button'
                                                            className="text-gray-500 hover:text-gray-600 darks:hover:text-gray-400 transition-colors duration-200 focus:outline-none hover:outline-none bg-gray-100 px-4 py-1 hover:scale-105" onClick={openEditProfile}
                                                        >

                                                            Edit <MdAddAPhoto className="inline" />
                                                        </button> */}
                                                    </div>
                                                </div>
                                                <div className="grid grid-cols-12">
                                                    <div className="py-3 col-span-12 md:col-span-6">
                                                        <div className=" my-3" />
                                                        <div className="ltr:text-left rtl:text-right">
                                                            <div className="mb-2 flex">
                                                                <div className='flex-1'> <strong> Name :</strong></div>
                                                                <div className='flex-1'><span className="ml-2"> {fetchData?.name}</span></div>
                                                            </div>
                                                            <div className="mb-2 flex mt-4">
                                                                <div className='flex-1'>
                                                                    <strong>Mobile :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">(+91) {fetchData?.mobile}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-4">
                                                                <div className='flex-1'>
                                                                    <strong>Email :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">{fetchData?.email}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-4">
                                                                <div className='flex-1'>
                                                                    <strong>Date Of Birth :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">{fetchData?.dob == '' || fetchData?.dob == null || fetchData?.dob == undefined ? 'N/A' : fetchData?.dob}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-4">
                                                                <div className='flex-1'>
                                                                    <strong>Gender :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">{fetchData?.gender == '' || fetchData?.gender == null || fetchData?.gender == undefined ? 'N/A' : fetchData?.gender}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-4">
                                                                <div className='flex-1'>
                                                                    <strong>Aadhar No. :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">{fetchData?.aadhar == '' || fetchData?.aadhar == null || fetchData?.aadhar == undefined ? 'N/A' : fetchData?.aadhar}</span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="py-3 col-span-12 md:col-span-6">
                                                        <div className="my-3" />
                                                        <div className="ltr:text-left rtl:text-right">
                                                            <div className="mb-2 flex">
                                                                <div className='flex-1'>
                                                                    <strong> Specially Abled ? :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2"> {fetchData?.is_specially_abled ? 'Yes' : 'No'}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-3">
                                                                <div className='flex-1'>
                                                                    <strong> Armed Force... :</strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <span className="ml-2">{fetchData?.is_armed_force ? 'Yes' : 'No'}</span>
                                                                </div>
                                                            </div>
                                                            <div className="mb-2 flex mt-3">
                                                                <div className='flex-1'>
                                                                    <strong>Aadhar Document  : </strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <GrDocumentPdf onClick={''} className='text-3xl inline text-indigo-500 font-bold cursor-pointer hover:scale-105' />
                                                                </div>

                                                            </div>
                                                            {fetchData?.is_specially_abled && <div className="mb-2 flex mt-3">
                                                                <div className='flex-1'>
                                                                    <strong>Specially Abled Document  : </strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <GrDocumentPdf onClick={''} className='text-3xl inline text-indigo-500 font-bold cursor-pointer hover:scale-105' />
                                                                </div>
                                                            </div>}
                                                            {fetchData?.is_armed_force && <div className="mb-2 flex mt-3">
                                                                <div className='flex-1'>
                                                                    <strong>Armed Force Document : </strong>
                                                                </div>
                                                                <div className='flex-1'>
                                                                    <GrDocumentPdf onClick={''} className='text-3xl inline text-indigo-500 font-bold cursor-pointer hover:scale-105' />
                                                                </div>
                                                            </div>}

                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="flex flex-row w-full border-t border-gray-200 pt-10">
                                                        <div className='flex-1'>
                                                            <button className='shadow-xl cursor-pointer 2xl:text-base text-sm bg-indigo-500 text-white px-2 py-1 rounded-sm hover:bg-indigo-600' onClick={() => navigate('/change-password/change')}>Change Password</button>
                                                        </div>
                                                        <div className='flex-1 text-right'>
                                                            <button className='shadow-xl cursor-pointer 2xl:text-base text-sm bg-indigo-500 text-white px-2 py-1 rounded-sm hover:bg-indigo-600' onClick={() => navigate('/landingDashboard')}>View Dashboard</button>
                                                        </div>
                                                    </div>
                                            </div>
                                        </div>

                                        {/* your productivity section */}
                                        {/* <div className="flex-shrink max-w-full px-4 w-full lg:w-2/3 mb-8">
                                            <div className="bg-white darks:bg-gray-800 rounded-lg shadow-lg h-full p-6">
                                                <div className="flex flex-row justify-between pb-3">
                                                    <div className="flex flex-col">
                                                        <h3 className="text-base font-bold">Notification</h3>
                                                    </div>
                                                </div>

                                                <NotificationComponent />
                                                <canvas className="max-w-100" id="ProductifityLine" />
                                            </div>
                                        </div> */}


                                    </div>
                                    {/* Modal */}
                                </div>
                            </div>
                        </div>
                    </main>
                </div>
                <div className='pb-12'>

                </div>


                {/* popup to edit profile */}
                <div className={`${openEditScreen} `}>
                    <div class="z-50  overflow-auto inset-0 w-full h-full fixed py-6 mx-auto bg-transparent">
                        <div class="z-50 relative p-3 mx-auto my-0 w-3/6" >

                            <form class="valid-form bg-white darks:bg-gray-800 rounded-lg shadow-lg border darks:border-gray-800 flex flex-col overflow-hidden" onSubmit={formik.handleSubmit} onChange={handleChange}>
                                <div className='flex border-b darks:border-gray-800'>
                                    <div className='flex-1'>
                                        <h1 class="px-6 py-3 text-xl font-bold ">Edit profile</h1>
                                    </div>
                                    <div className='flex-1'>
                                        <button type='button' class="fill-current h-6 w-6 top-0 m-4 float-right font-6xl font-bold  " onClick={handleCloseForm}><svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-6 h-6 text-red-500">
                                            <path stroke-linecap="round" stroke-linejoin="round" d="M9.75 9.75l4.5 4.5m0-4.5l-4.5 4.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                                        </svg>
                                        </button>
                                    </div>
                                </div>


                                <div class="p-6 flex-grow">
                                    <div class="flex flex-wrap flex-row -mx-4">
                                        <div class="flex-shrink max-w-full px-4 w-full">
                                            <div class="bg-white darks:bg-gray-800 rounded-lg overflow-hidden pb-8">

                                                <div class="flex justify-center mt-10 relative">
                                                    <div className="form-group  flex justify-center relative mt-5">
                                                        <div className='w-28 h-28 border rounded-full'>
                                                            <img className='w-28 h-28 rounded-full border bg-gray-700 shadow-xl' src={photoViewUrl} alt="" />
                                                            <span className='absolute bottom-2 left-0 right-0 text-center '>
                                                                <span onClick={() => photoRef.current.click()} className='bg-white text-indigo-500 px-1 border rounded-xl text-xs cursor-pointer font-semibold hover:scale-105'>Change</span>
                                                            </span>
                                                            <span className="text-red-600 absolute text-xs -bottom-3 text-center">{formik.touched.photo && formik.errors.photo ? formik.errors.photo : null}</span>
                                                        </div>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>



                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Name</label>
                                            <input type="text" {...formik.getFieldProps('name')} class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.name && formik.errors.name ? formik.errors.name : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Mobile No.</label>
                                            <input {...formik.getFieldProps('mobile')} type="text" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-gray-50 border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.mobile && formik.errors.mobile ? formik.errors.mobile : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Email</label>
                                            <input {...formik.getFieldProps('email')} type="email" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-gray-50 border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.email && formik.errors.email ? formik.errors.email : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Aadhar</label>
                                            <input {...formik.getFieldProps('aadhar')} type="text" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.aadhar && formik.errors.aadhar ? formik.errors.aadhar : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">DOB</label>
                                            <input {...formik.getFieldProps('dob')} type="date" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.dob && formik.errors.dob ? formik.errors.dob : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Gender</label>
                                            <select {...formik.getFieldProps('gender')} class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer"  >


                                                <option>select</option>
                                                <option value="Male">Male</option>
                                                <option value="Female">Female</option>
                                                <option value="Other">Other</option>
                                            </select>
                                            <span className="text-red-600 absolute text-xs">{formik.touched.gender && formik.errors.gender ? formik.errors.gender : null}</span>
                                        </div>


                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Is-Specially-Abled</label>
                                            <select {...formik.getFieldProps('isSpeciallyAbled')} type="text" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" >
                                                <option value="1">Yes</option>
                                                <option value="0">No</option>
                                            </select>
                                            <span className="text-red-600 absolute text-xs">{formik.touched.isSpeciallyAbled && formik.errors.isSpeciallyAbled ? formik.errors.isSpeciallyAbled : null}</span>
                                        </div>
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Is-Armed-Force</label>
                                            <select {...formik.getFieldProps('isArmedForce')} type="text" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" >
                                                <option value="1">Yes</option>
                                                <option value="0">No</option>
                                            </select>
                                            <span className="text-red-600 absolute text-xs">{formik.touched.isArmedForce && formik.errors.isArmedForce ? formik.errors.isArmedForce : null}</span>
                                        </div>

                                        {/* <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <div className="form-group  flex justify-center relative mt-5">
                                                <div className='w-28 h-28 border rounded-sm'>
                                                    <img className='w-28 h-28 rounded-full border bg-gray-700 shadow-xl' src={aadharViewUrl} alt="" />
                                                    <span className='absolute bottom-2 left-0 right-0 text-center '>
                                                        <span onClick={() => aadharRef.current.click()} className='bg-white text-indigo-500 px-1 border rounded-xl text-xs cursor-pointer font-semibold hover:scale-105'>Change</span>
                                                    </span>
                                                    <span className="text-red-600 absolute text-xs -bottom-3 text-center">{formik.touched.aadharDoc && formik.errors.aadharDoc ? formik.errors.aadharDoc : null}</span>
                                                </div>

                                            </div>
                                            <div className='text-center mt-2'>Aadhar Document</div>
                                        </div> */}

                                        {/* // THIS IS HIDDEN INPUT FOR AADHAR DOCUMENT  */}
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6 relative">
                                            <label class="inline-block mb-2">Aadhar Document</label>
                                            <input ref={aadharRef} {...formik.getFieldProps('aadharDoc')} type="file" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.aadharDoc && formik.errors.aadharDoc ? formik.errors.aadharDoc : null}</span>
                                        </div>

                                        {/* <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <div className="form-group  flex justify-center relative mt-5">
                                                <div className='w-28 h-28 border rounded-full'>
                                                    <img className='w-28 h-28 rounded-full border bg-gray-700 shadow-xl' src={armedViewUrl} alt="" />
                                                    <span className='absolute bottom-2 left-0 right-0 text-center '>
                                                        <span onClick={() => armedRef.current.click()} className='bg-white text-indigo-500 px-1 border rounded-xl text-xs cursor-pointer font-semibold hover:scale-105'>Change</span>
                                                    </span>
                                                    <span className="text-red-600 absolute text-xs -bottom-3 text-center">{formik.touched.armedForceDoc && formik.errors.armedForceDoc ? formik.errors.armedForceDoc : null}</span>
                                                </div>

                                            </div>
                                            <div className='text-center mt-2'>Armed Force Document</div>

                                        </div> */}

                                        {armedStatus && <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Armed Force Document</label>
                                            <input ref={armedRef} {...formik.getFieldProps('armedForceDoc')} type="file" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" />
                                            {/* <span className="text-red-600 absolute text-xs">{formik.touched.armedForceDoc && formik.errors.armedForceDoc ? formik.errors.armedForceDoc : null}</span> */}
                                        </div>}

                                        {/* <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <div className="form-group  flex justify-center relative mt-5">
                                                <div className='w-28 h-28 border rounded-full'>
                                                    <img className='w-28 h-28 rounded-full border bg-gray-700 shadow-xl' src={specialViewUrl} alt="" />
                                                    <span className='absolute bottom-2 left-0 right-0 text-center '>
                                                        <span onClick={() => specialRef.current.click()} className='bg-white text-indigo-500 px-1 border rounded-xl text-xs cursor-pointer font-semibold hover:scale-105'>Change</span>
                                                    </span>
                                                    <span className="text-red-600 absolute text-xs -bottom-3 text-center">{formik.touched.speciallyAbledDoc && formik.errors.speciallyAbledDoc ? formik.errors.speciallyAbledDoc : null}</span>
                                                </div>

                                            </div>
                                            <div className='text-center mt-2'>Specially Abled Document</div>

                                        </div> */}
                                        {specialStatus && <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Specially Abled Document</label>
                                            <input ref={specialRef} {...formik.getFieldProps('speciallyAbledDoc')} type="file" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600 cursor-pointer" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.speciallyAbledDoc && formik.errors.speciallyAbledDoc ? formik.errors.speciallyAbledDoc : null}</span>
                                        </div>}
                                        <div class="form-group flex-shrink max-w-full px-4 w-full md:w-1/2 mb-6">
                                            <label class="inline-block mb-2">Photo</label>
                                            <input ref={photoRef} {...formik.getFieldProps('photo')} type="file" class="w-full leading-5 relative py-2 px-4 rounded text-gray-800 bg-white border border-gray-300 overflow-x-auto focus:outline-none focus:border-gray-400 focus:ring-0 darks:text-gray-300 darks:bg-gray-700 darks:border-gray-700 darks:focus:border-gray-600" />
                                            <span className="text-red-600 absolute text-xs">{formik.touched.photo && formik.errors.photo ? formik.errors.photo : null}</span>
                                        </div>

                                    </div>
                                </div>
                                <div class="px-6 py-3 border-t darks:border-gray-800 flex justify-end">
                                    <button type="button" class="py-2 px-4 inline-block text-center rounded leading-5 text-gray-800 bg-gray-100 border border-gray-100 hover:text-gray-900 hover:bg-gray-200 hover:ring-0 hover:border-gray-200 focus:bg-gray-200 focus:border-gray-200 focus:outline-none focus:ring-0 ltr:mr-2 rtl:ml-2" onClick={handleCloseForm}>Close</button>
                                    <button type="submit" class="py-2 px-4 inline-block text-center rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0">Update</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div >
            </div>
        </>
    )
}

export default EditProfile